import React, {useState} from 'react';
import Video from '../../assets/video/video.mp4'
import {
  ArrowForward,
  ArrowRight,
  HeroBg,
  HeroBtnWrapper,
  HeroContainer,
  HeroContent,
  HeroH1,
  HeroP,
  VideoBg
} from "./HeroElements";
import {Button} from "../common/ButtonElement";

const HeroSection = () => {

  const [hover, setHover] = useState(false);

  const onHover = () => {
    setHover(!hover)
  }

  return (
    <HeroContainer id='home'>
      <HeroBg>
        <VideoBg autoPlay loop muted src={Video} type='video/mp4'/>
      </HeroBg>
      <HeroContent>
        <HeroH1>My Portfolio WebApp</HeroH1>
        <HeroP>To navigate this site you can use the menu in the header or the button below</HeroP>
        <HeroBtnWrapper>
          <Button to='about' onMouseEnter={onHover} onMouseLeave={onHover} primary='true' dark={hover ? 1 : 0}
                  smooth={true} duration={500} spy={true} exact='true'
                  offset={-80}>
            Get started {hover ? <ArrowForward/> : <ArrowRight/>}
          </Button>
        </HeroBtnWrapper>
      </HeroContent>
    </HeroContainer>
  )
};

export default HeroSection;