import React from 'react';
import {
  CloseIcon,
  Icon,
  SidebarContainer,
  SidebarLink,
  SidebarMenu,
  SidebarRoute,
  SidebarWrapper,
  SideBtnWrap
} from "./SidebarElements";

const Sidebar = ({isOpen, toggle}) => {
  return (
    <SidebarContainer isOpen={isOpen} onClick={toggle}>
      <Icon onClick={toggle}>
        <CloseIcon/>
      </Icon>
      <SidebarWrapper>
        <SidebarMenu>
          <SidebarLink onClick={toggle} to='about' smooth={true} duration={500} spy={true} exact='true'
                       offset={-80}>About</SidebarLink>
          <SidebarLink onClick={toggle} to='technologies' smooth={true} duration={500} spy={true} exact='true'
                       offset={-80}>Technologies</SidebarLink>
          <SidebarLink onClick={toggle} to='projects' smooth={true} duration={500} spy={true} exact='true'
                       offset={-80}>Projects</SidebarLink>
          <SidebarLink onClick={toggle} to='webapp' smooth={true} duration={500} spy={true} exact='true'
                       offset={-80}>WebApp</SidebarLink>
        </SidebarMenu>
        <SideBtnWrap>
          <SidebarRoute to='/signin'>Sign In</SidebarRoute>
        </SideBtnWrap>
      </SidebarWrapper>
    </SidebarContainer>
  );
}

export default Sidebar;
