import React from 'react';
import {
    FooterContainer,
    FooterLink,
    FooterLinkItems,
    FooterLinksContainer,
    FooterLinksWrapper,
    FooterLinkTitle,
    FooterWrap,
    SocialIconLink,
    SocialIcons,
    SocialLogo,
    SocialMedia,
    SocialMediaWrap,
    WebsiteRights
} from "./FooterElements";
import {FaGithub, FaSkype, FaViber, FaLinkedin, FaTelegram} from "react-icons/fa";
import {animateScroll as scroll} from "react-scroll";

const Footer = () => {

    const toggleHome = () => {
        scroll.scrollToTop();
    }

    return (
        <FooterContainer>
            <FooterWrap>
                <FooterLinksContainer>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>These</FooterLinkTitle>
                            <FooterLink to='/'>Are</FooterLink>
                            <FooterLink to='/'>Footer</FooterLink>
                            <FooterLink to='/'>Links</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLinkTitle>They</FooterLinkTitle>
                            <FooterLink to='/'>Are made</FooterLink>
                            <FooterLink to='/'>For</FooterLink>
                            <FooterLink to='/'>Design</FooterLink>
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>They</FooterLinkTitle>
                            <FooterLink to='/'>Have</FooterLink>
                            <FooterLink to='/'>No</FooterLink>
                            <FooterLink to='/'>Functionality</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLinkTitle>You can</FooterLinkTitle>
                            <FooterLink to='/'>Safely</FooterLink>
                            <FooterLink to='/'>Click</FooterLink>
                            <FooterLink to='/'>On them</FooterLink>
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                </FooterLinksContainer>
                <SocialMedia>
                    <SocialMediaWrap>
                        <SocialLogo to='/' onClick={toggleHome}>Portfolio.js</SocialLogo>
                        <WebsiteRights>Portfolio.js &copy; {new Date().getFullYear()} All rights
                            reserved.</WebsiteRights>
                        <SocialIcons>
                            <SocialIconLink href='https://github.com/dizmay' target='_blank' aria-label='Github'>
                                <FaGithub/>
                            </SocialIconLink>
                            <SocialIconLink href='https://t.me/dizmayyy' target='_blank' aria-label='Telegram'>
                                <FaTelegram/>
                            </SocialIconLink>
                            <SocialIconLink href='viber://chat?number=%2B375292229367' target='_blank' aria-label='Skype'>
                                <FaViber/>
                            </SocialIconLink>
                            <SocialIconLink href='https://join.skype.com/invite/hScXMQw7wgNZ' target='_blank'
                                            aria-label='Skype'>
                                <FaSkype/>
                            </SocialIconLink>
                            <SocialIconLink href='https://www.linkedin.com/in/peter-stukalov-2459b31bb/' target='_blank'
                                            aria-label='Linkedin'>
                                <FaLinkedin/>
                            </SocialIconLink>
                        </SocialIcons>
                    </SocialMediaWrap>
                </SocialMedia>
            </FooterWrap>
        </FooterContainer>
    )
};

export default Footer;