import React from 'react';
import Icon1 from '../../assets/svg/svg-4.svg';
import Icon2 from '../../assets/svg/svg-5.svg';
import Icon3 from '../../assets/svg/svg-6.svg';
import {
  ProjectsLink,
  ProjectsCard,
  ProjectsContainer,
  ProjectsH1,
  ProjectsH2,
  ProjectsIcon,
  ProjectsP,
  ProjectsWrapper
} from "./ProjectsElements";
import {Button} from "../common/ButtonElement";
import {FaArrowDown} from "react-icons/fa";

const Projects = () => {
  return (
    <ProjectsContainer id='projects'>
      <ProjectsH1>My projects:</ProjectsH1>
      <ProjectsWrapper>
        <ProjectsLink href='https://dizmay.github.io/todolist/' target='_blank' aria-label='Todo List'>
          <ProjectsCard>
            <ProjectsIcon src={Icon1}/>
            <ProjectsH2>Todo List</ProjectsH2>
            <ProjectsP>(React)</ProjectsP>
          </ProjectsCard>
        </ProjectsLink>
        <ProjectsLink>
          <ProjectsCard>
            <ProjectsIcon src={Icon2}/>
            <ProjectsH2>Social Network</ProjectsH2>
            <ProjectsP>This project is in progress</ProjectsP>
          </ProjectsCard>
        </ProjectsLink>
        <ProjectsLink href='https://dizmay.github.io/tag-game/' target='_blank' aria-label='Calculator'>
          <ProjectsCard>
            <ProjectsIcon src={Icon3}/>
            <ProjectsH2>Puzzle game</ProjectsH2>
            <ProjectsP>(Native JS)</ProjectsP>
          </ProjectsCard>
        </ProjectsLink>
      </ProjectsWrapper>
      <Button to='webapp' primary={1} dark={1} big={1} fontbig={1} smooth={true} duration={500} spy={true} exact='true'
              offset={-80}>
        <FaArrowDown/>
      </Button>
    </ProjectsContainer>
  )
};

export default Projects;